import React, { useState } from 'react';

import QRCode from 'qrcode';

// var QrReader = require('qrreader');

const Generatecode = () => {
  // generate qr
  const [text, setText] = useState('');
  const [image, setImage] = useState('');
  const Generateqr = async () => {
    try {
      const Scan = await QRCode.toDataURL(text);
      setImage(Scan);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <div className="my-5 row mx-auto text-center">
        <div className="col-12">
          <input type="text" onChange={(e) => setText(e.target.value)}></input>
          <button className=" btn btn-primary" onClick={() => Generateqr()}>
            Click to Generate
          </button>
        </div>
        <div className="col-12">
          <br />
          <p style={{ marginTop: 20 }}>click image to download</p>
          <div className="text-center">
            {image ? (
              <a href={image} download>
                <img src={image} alt="img/" />
              </a>
            ) : null}
          </div>
        </div>
      </div>
    </>
  );
};

export default Generatecode;
