import { BiQrScan, BiScan } from 'react-icons/bi';
import { Link } from 'react-router-dom';

const Home = () => {
  return (
    <>
      <div className="row text-center">
        <div className="col-6" style={{ fontSize: '200px' }}>
          <Link to="/Generate">
            <BiQrScan />
          </Link>
          <h3>Generate Qr Code</h3>
        </div>
        <div className="col-6" style={{ fontSize: '200px' }}>
          <Link to="/Scanner">
            <BiScan />
          </Link>
          <h3>Scan Qr Code</h3>
        </div>
      </div>
    </>
  );
};

export default Home;
