import React, { useState } from 'react';
import { QrReader } from 'react-qr-reader';
import Alert from 'react-bootstrap/Alert';

const Scanner = (props) => {
  const [data, setData] = useState('No result');

  return (
    <>
      <Alert className="text-center">Jika Kamera tidak muncul silahkan refresh halaman!</Alert>
      <div style={{ margin: 'auto', width: '50vw', height: '50vh' }}>
        <QrReader
          delay={300}
          onResult={(result, error) => {
            if (result) {
              setData(result?.text);
            }

            if (error) {
              console.log(error);
            }
          }}
        />
        <div className="scanner-result">
          <div style={{ fontSize: 18 }} rowsMax={4} defaultValue={data} value={data}>
            {data}
          </div>
        </div>
      </div>
    </>
  );
};

export default Scanner;
